@font-face {
    font-family: Montserrat;
    src: url('./fonts/Montserrat-Regular.ttf');
}

@font-face {
    font-family: MontLight;
    src: url('./fonts/Montserrat-Light.ttf');
}

button {
    font-family: Montserrat;
    background-color: rgb(230, 230, 230);
    border-radius: 5px;
}

body {
    width: 100vw;
    height: 100vh;
    background-image: url('./compute-ea4c57a4.png');
}

#root {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
}

.calculator {
    display: flex;
    flex-direction: column;
    height: fit-content;
    width: fit-content;
    border: 1px solid white;
    padding: 15px;
    gap: 5px;
    align-content: center;
    justify-content: center;
}

.displays {
    height: 70px;
    font-size: 20px;
    text-align: right;
    border-bottom: 1px solid black;
    margin-bottom: 10px;
    background-color: rgb(230, 230, 230);
    padding: 10px;
    border-radius: 5px;
}

.numbers-operands {
    display: grid;
    grid-template-columns: 2fr 0.9fr;
    gap: 5px;
}

.numbers, .operands {
    display: grid;
    grid-template-rows: auto auto auto auto;
    gap: 5px;
}

.numbers {
    grid-template-columns: auto auto auto;
}

.numbers button {
    width: 140px;
}

#zero {
    width: 100%;
}

.operands {
    grid-template-columns: auto auto;
}

button {
    width: 100%;
    height: 70px;
    font-size: larger;
}

#add, #subtract {
    font-family: MontLight;
    font-size: 30px;
}

#multiply {
    font-size: 25px;
    font-family: MontLight;
}

#zero {
    grid-column: 1 / span 2;
}

#clear {
    grid-column: 1 / span 2;
    background-color: red;
    color: white;
}

#equals {
    grid-column: 1 / span 2;
}